.skills-card{
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 2.5rem;
    text-align: center;
    background: rgba(22,17,47,0.398);
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
}

.skills-card:hover,
.skills-card:active{
    background: linear-gradient(180deg, #aa94fe 0%, #7c5fe6 100%);
}

.skills-card span{
    font-size: 1.3rem;
    font-weight: 500;
}

.skill-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #8160f9;
    /* background: linear-gradient(180deg, #aa94fe 0%, #7c5fe6 100%); */
    border-radius: 0.65rem;
    border: 1px solid #6852ba;
    position: absolute;
    top: -1.2rem;
    left: -1.2rem;
}

.skill-icon img{
    width: 2.4rem;
    height: auto;
    object-fit: contain;
    
}

@media(max-width: 1025px){
    .skills-card{
        padding: 2rem;
    }

    .skills-card span{
        font-size: 1rem;
    }

    .skill-icon{
        width: 3rem;
        height: 3rem;
    }

    .skill-icon img{
        width: 1.8rem;
    }
}

@media(max-width: 768px){
    .skills-card span{
        font-size: 0.9rem;
    }
    .skills-card{
        padding: 1.5rem;
    }
}

@media(max-width: 376px){
    .skills-card span{
        font-size: 0.8rem;
    }
    .skills-card{
        padding: 1rem;
    }
}

@media(max-width: 321px){
    .skills-card span{
        font-size: 0.66rem;
    }
    .skills-card{
        padding: 1.5rem;
    }
}



